.container {
    margin: 30px auto 0 auto;
    padding: 10px;
    background-color: transparent;
    backdrop-filter: blur(5px);
    border-radius: 25px;
    border: 2px outset #b0e5ff40;
    max-width: 700px;
}

.about {
    position: relative;
    padding: 40px 30px 30px 30px;
    font-size: 17px;
    text-align: center;
    color: white;
    font-style: italic;
    background-color: #28649540;
    border-radius: 20px;
    border: 2px inset #b0e5ff40;
    box-shadow: 1px 1px 4px #fff5, -1px -1px 4px #0434;
}

.about > footer {
    font-size: 15px;
    font-style: italic;
}

.volver {
    position: absolute;
    /* ↓ mi tercer intento (en vano) por colocar el ícono en el centro del botón :face-palm: */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    left: 5px;
    top: 5px;
    bottom: 5px;
    border: 2px outset #b0e5ff40;

    height: 30px;
    width: 30px;
    border-radius: 999px;
    font-size: 30px;
    font-weight: 100;
    color: #99d9ee80;


    background-color: transparent;
    transition: background-color 1s ease-out;
}

.volver:hover {
    background-color: #08496025;
    transition: background-color 0.2s;
}